(function() {
    'use strict';

    angular.module('BlurAdmin.pages.logistics')
        .factory('businessService', businessService);

    businessService.$inject = ['businessConst', 'formatDataService'];

    function businessService(businessConst, formatDataService) {

        return {
            calculateLiabilities: calculateLiabilities,
            calculateTotal: calculateTotal,
            calculateDeclareFee: calculateDeclareFee
        }

        function calculateTotal(subTotal, declareValue, deliveryPrice, discount, onHandFee, guaranteeFee) {
            var total = convertToNumber(subTotal) + convertToNumber(declareValue) + convertToNumber(deliveryPrice) + convertToNumber(onHandFee) + convertToNumber(guaranteeFee) - convertToNumber(discount);
            return formatCurrency(total);
        }

        function calculateLiabilities(finalTotal, prepaid) {
            var liabilities = convertToNumber(finalTotal) - convertToNumber(prepaid);
            return formatCurrency(liabilities);
        }

        function calculateDeclareFee(declareValue) {
            var declareFee = convertToNumber(declareValue) * businessConst.intDeclaredFee;
            return formatCurrency(declareFee);
        }

        function convertToNumber(numberString) {
            return formatDataService.convertToNumber(numberString);
        };

        function formatCurrency(currency) {
            return formatDataService.formatCurrency(currency);
        };
    }
})();
(function() {
    'use strict';
    angular.module('BlurAdmin.pages')
        .factory('utility', utilityService);

    utilityService.$inject = ['$http', 'Url'];

    function utilityService($http, Url) {
        return {
            getData: getData,
            postData: postData
        };

        function postData(controller, dataObject) {
            return $http.post(Url.hostDomain + controller, dataObject).then(function() {

            });
        };

        function getData(controller) {
            var data = {};
            return $http.get(Url.hostDomain + controller).then(
                function(response) {
                    data = response.data;
                    return data;
                });
        }
    }
})();
(function() {
    'use strict';

    angular.module('BlurAdmin.pages.logistics').factory('shareDataService', shareDataService);

    shareDataService.$inject = ['$rootScope']

    function shareDataService($rootScope) {
        var dataList = {};
        var initDataList = [];
        var bolList = [];
        var branchCode = {};
        var branchList = [];
        var stampCodeList = [];
        var bolCode = {};

        return {
            getInitData: getInitData,
            addInitData: addInitData,
            getBranchCode: getBranchCode,
            addBranchCode: addBranchCode,
            addBolCode: addBolCode,
            getBolCode: getBolCode,
            addItem: addItem,
            getList: getList,
            getAllBol: getAllBol,
            addAllBol: addAllBol,
            addBranch: addBranch,
            getAllBranch: getAllBranch,
            addStampCode: addStampCode,
            getStampCode: getStampCode
        };

        /**init data { get; set; } */
        function getInitData() {
            return initDataList;
        }

        function addInitData(initData) {
            initDataList.push(initData);
        }
        /**end */

        /**branchCode { get; set; } */
        function addBranchCode(branch) {
            branchCode = branch;
        }

        function getBranchCode() {
            return branchCode;
        }
        /**end */

        /**Bol Code { get; } */
        function addBolCode(code) {
            bolCode = code;
        }

        function getBolCode() {
            return bolCode;
        }

        /**get all Bol { get; set; } */
        function addAllBol(bol) {
            bolList.push(bol);
        }

        function getAllBol() {
            return bolList;
        }
        /**end */

        /** */
        function addItem(item) {
            dataList = item;
        }

        function getList() {
            return dataList;
        }

        /**get branch { get; set; } */
        function addBranch(branch) {
            branchList.push(branch);
        }

        function getAllBranch() {
            return branchList;
        }
        /**end */

        function addStampCode(stampCode) {
            stampCodeList.push(stampCode);
        }

        function getStampCode() {
            return stampCodeList;
        }
    }
})();
(function() {
    'use strict';

    angular.module('BlurAdmin.pages.logistics')
        .factory('formatDataService', formatDataService);

    function formatDataService() {

        return {
            convertToNumber: convertToNumber,
            formatCurrency: formatCurrency
        }

        /** function convertToNumber
         *  @Description: using to convert currency String to number
         *  @Param: string numberString
         *  @Return: number (Ex: 10000)
         */
        function convertToNumber(numberString) {
            if (numberString == "" || numberString == null) {
                return 0;
            } else {
                return parseInt(numberString.replace(/,/g, ""));
            }
        };

        /** function formatCurrency
         *  @Description: using to convert number to currency String 
         *  @Param: number currency
         *  @Return: string (Ex:10,000)
         */
        function formatCurrency(currency) {
            return currency.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,");
        }
    }
})();
